import React, { Component } from "react";
import AutoResponsive from "autoresponsive-react";
import ReactDOM from "react-dom";
class Portfolio extends Component {
  constructor(props) {
    super(props);
    this.state = {
      project: [],
      warp: 0,
      index: 1
    };
    this.project = [];
    this.wrap = 0;
    this.autoBox = null;
    this.index = 0;
    this.unResize = false;
    this.resizeBeforeWidth = 0;
  }

  getAutoResponsiveProps() {
    return {
      itemMargin: 20,
      containerWidth: this.state.containerWidth || this.wrap,
      itemClassName: "item",
      gridWidth: 1,
      transitionDuration: ".5"
    };
  }

  componentDidMount() {
    window.addEventListener(
      "resize",
      e => {
        
        setTimeout(() => {
          if (document.body.clientWidth == this.resizeBeforeWidth) {
            return false;
          }
          this.resizeBeforeWidth = document.body.clientWidth;
          this.wrap = ReactDOM.findDOMNode(this.autoBox).clientWidth;
          this.setState(
            {
              containerWidth: ReactDOM.findDOMNode(this.autoBox).clientWidth
            },
            () => {
              setTimeout(() => {
                this.mapCreateImg(this.project);
              }, 100);
            }
          );
        });
      },
      false
    );
  }

  componentWillReceiveProps(nextState) {
    this.mapCreateImg(nextState.data.projects);
  }

  mapCreateImg(arr) {
    this.project = [];
    arr.map((projects, index, data) => {
      var projectImage = "images/portfolio/" + projects.image;
      this.createImgProject(projectImage, data[index], data);
    });
  }

  isPhone() {
    var ua = navigator.userAgent.toLowerCase();
    let phone = false;
    if (/android|adr/gi.test(ua)) {
      phone = true;
    } else if (/\(i[^;]+;( U;)? CPU.+Mac OS X/gi.test(ua)) {
      phone = true;
    } else if (/iPad/gi.test(ua)) {
      phone = true;
    }
    return phone;
  }
  componentWillMount() {
    this.resizeBeforeWidth = document.body.clientWidth;
  }

  getBoxStyle(img, projectImage, activeData) {
    let phone = this.isPhone();
    let scale = phone ? 0.8 : 0.22;
    let width = this.wrap * scale;
    let marginLeft = phone ? (this.wrap - width) / 2 : 0;
    let scaleWidth = width / img.width;
    let height = scaleWidth * img.height;
    let data = {
      name: projectImage,
      ["style"]: {
        width: width,
        height: height,
        padding: 0
      },
      image: img,
      imageUrl: projectImage,
      marginLeft: marginLeft,
      ...activeData
    };
    data.style = {
      width: width,
      marginLeft: marginLeft,
      height: height,
      padding: 0
    };
    return data;
  }

  createImgProject(projectImage, activeData, allData) {
    let { project } = this.state;
    var img = new Image();
    img.src = projectImage;
    img.crossOrigin = "*";
    img.onload = () => {
      let style = this.getBoxStyle(img, projectImage, activeData);
      this.project.push({ ...style, id: this.index++ });
      if (this.project.length == allData.length) {
        this.setState(
          {
            project: [...this.project]
          },
          () => {
            this.unResize = false;
          }
        );
      }
    };
  }

  render() {
    if (this.state.project) {
      let project = this.state.project.map((item, index) => {
        return (
          <div
            style={item.style}
            key={item.id}
            className="newColumns portfolio-item"
          >
            <div className="item-wrap">
              <a href={item.url} title={item.title}>
                <img
                  crossOrigin="*"
                  style={{ width: "100%" }}
                  src={item.imageUrl}
                ></img>
                <div className="overlay">
                  <div className="portfolio-item-meta">
                    <h5>{item.title}</h5>
                    <p>{item.category}</p>
                    <p style={{ marginTop: "20px" }}>{item.skills}</p>
                  </div>
                </div>
                <div className="link-icon">
                  <i className="fa fa-link"></i>
                </div>
              </a>
            </div>
          </div>
        );
      });
      var projects = (
        <AutoResponsive
          ref={el => {
            if (ReactDOM.findDOMNode(el)) {
              this.autoBox = el;
              if (this.wrap == 0) {
                this.wrap = ReactDOM.findDOMNode(el).clientWidth;
              }
            }
          }}
          {...this.getAutoResponsiveProps(this.phone ? 200 : 10)}
        >
          {project}
        </AutoResponsive>
      );
    }
    return (
      <section id="portfolio" style={{ paddingTop: 50 }}>
        <div className="row">
          <div className="twelve columns collapsed">
            <div
              id="portfolio-wrapper"
              className="bgrid-quarters s-bgrid-thirds cf"
            >
              {projects}
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default Portfolio;



